import { defineEventHandler } from 'h3'

export default defineEventHandler(async (event) => {
  try {
    // 檢查 Cloudflare context 是否存在
    const hasCloudflare = !!event.context.cloudflare
    const hasEnv = !!event.context.cloudflare?.env
    const hasDB = !!event.context.cloudflare?.env?.DB
    
    // 如果有 D1，嘗試執行簡單查詢
    let dbTest = null
    if (hasDB) {
      try {
        const db = event.context.cloudflare.env.DB
        const result = await db.prepare("SELECT name FROM sqlite_master WHERE type='table' LIMIT 5").all()
        dbTest = {
          success: true,
          tables: result.results?.map((r: any) => r.name) || [],
          message: 'D1 database connected successfully'
        }
      } catch (error: any) {
        dbTest = {
          success: false,
          error: error.message,
          message: 'D1 database connection failed'
        }
      }
    }
    
    return {
      environment: {
        hasCloudflare,
        hasEnv,
        hasDB,
        isDev: process.dev,
        isProduction: process.env.NODE_ENV === 'production',
        runtime: process.versions?.node ? 'Node.js' : 'Cloudflare Workers'
      },
      database: dbTest || {
        success: false,
        message: 'D1 database not available - use "npm run dev:wrangler" for local development with D1'
      },
      instructions: {
        local: 'For local development with D1: npm run dev:wrangler',
        production: 'For production: npm run deploy'
      }
    }
  } catch (error: any) {
    return {
      error: true,
      message: error.message,
      stack: process.dev ? error.stack : undefined
    }
  }
})