import { defineEventHandler, createError } from 'h3'
import { getD1 } from '../../utils/d1'

export default defineEventHandler(async (event) => {
  try {
    const db = getD1(event)
    
    // 取得所有表格
    const tables = await db.prepare(`
      SELECT name, sql 
      FROM sqlite_master 
      WHERE type='table' 
      ORDER BY name
    `).all()
    
    // 為每個表格取得記錄數量和結構
    const tablesWithInfo = await Promise.all(
      (tables.results || []).map(async (table: any) => {
        // 取得記錄數量
        const countResult = await db.prepare(`SELECT COUNT(*) as count FROM ${table.name}`).first() as { count: number }
        
        // 取得表格結構
        const columns = await db.prepare(`PRAGMA table_info(${table.name})`).all()
        
        return {
          name: table.name,
          sql: table.sql,
          count: countResult?.count || 0,
          columns: columns.results?.map((col: any) => ({
            name: col.name,
            type: col.type,
            notnull: col.notnull === 1,
            default: col.dflt_value,
            primary_key: col.pk === 1
          })) || []
        }
      })
    )
    
    return {
      success: true,
      tables: tablesWithInfo
    }
  } catch (error: any) {
    console.error('Database tables error:', error)
    throw createError({
      statusCode: 500,
      statusMessage: `無法取得表格資訊: ${error.message}`
    })
  }
})